import Component from 'vue-class-component'
import {Vue} from 'vue-property-decorator'
import LineIndicator from "@/model/indicators/LineIndicator"
import {Data, IndicatorType} from "@/shared/data";
import {CriminalCharts} from "@/shared/CriminalCharts";
import {AdministrativeCharts} from "@/shared/AdministrativeCharts";
import {IndicatorChart} from "@/shared/interfaces";
import {AccidentCharts} from "@/shared/AccidentCharts";
import {CivilCharts} from "@/shared/CivilCharts";
import {ErsopCharts} from "@/shared/ErsopCharts";
import {DEFAULT_MONTH} from "@/shared/—Åonstants";

@Component({
    components: {
        'v-line-indicator': LineIndicator
    }
})
export default class Home extends Vue {
    private preparingData = false
    private chartCrimeTotal: IndicatorChart = CriminalCharts.chartTotal({labels: [], values: []})
    private chartAdministrativeTotal: IndicatorChart = AdministrativeCharts.chartTotal({labels: [], values: []})
    private chartAccidentTotal: IndicatorChart = AccidentCharts.chartTotal({labels: [], values: []})
    private chartCivilTotal: IndicatorChart = CivilCharts.chartTotal({labels: [], values: []})
    private chartErsopTotal: IndicatorChart = ErsopCharts.chartTotal({labels: [], values: []})

    async mounted() {
        if (!Data.isMonthFetched(IndicatorType.CRIME, DEFAULT_MONTH)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.CRIME,DEFAULT_MONTH)
        }
        if (!Data.isMonthFetched(IndicatorType.ADMINISTRATIVE, 3)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.ADMINISTRATIVE,3)
        }
        if (!Data.isMonthFetched(IndicatorType.ACCIDENT, 1)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.ACCIDENT,1)
        }
        if (!Data.isMonthFetched(IndicatorType.CIVIL, 12)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.CIVIL,12)
        }
        if (!Data.isMonthFetched(IndicatorType.ERSOP, 12)) {
            this.preparingData = true
            await Data.fetchData(IndicatorType.ERSOP,12)
        }
        this.preparingData = false
        this.chartCrimeTotal = CriminalCharts.chartTotal(Data.getYearsData(IndicatorType.CRIME,1,'19', DEFAULT_MONTH))
        this.chartAdministrativeTotal = AdministrativeCharts.chartTotal(Data.getYearsData(IndicatorType.ADMINISTRATIVE,1,'19', 3))
        this.chartAccidentTotal = AccidentCharts.chartTotal(Data.getYearsData(IndicatorType.ACCIDENT,7,'19', 1))
        this.chartCivilTotal = CivilCharts.chartTotal(Data.getYearsData(IndicatorType.CIVIL,1,'19', 12))
        this.chartErsopTotal = ErsopCharts.chartTotal(Data.getYearsData(IndicatorType.ERSOP,1,'19', 12))
    }

}
